import styled from 'styled-components';
import NavState from '../../context/navState';
import MainMenu from '../Button/BurgerButton/MainMenu';
import React from 'react';
import {buttonBlock, informBlockHeader, imgHeader} from '../Button/BurgerButton/SideMenu';




const Header =()=>{



  return (
    <S.Head id='top'>
      <S.HeadBodyLeft>
        <S.HeadLeftBlock>
          {imgHeader()}
        </S.HeadLeftBlock>
      </S.HeadBodyLeft>
      <S.HeadBodyCenter>
        <S.HeadCenterBlock>
          <div className='burger'> 
            <NavState >
              <MainMenu/>
            </NavState>
          </div>
          <div className='full'>
              {buttonBlock()}
          </div>
        </S.HeadCenterBlock>
      </S.HeadBodyCenter>
      <S.HeadBodyRight>
        {informBlockHeader()}
      </S.HeadBodyRight>
    </S.Head>
  )
};

export default Header;


const S = {
    Head: styled.header`
    display: flex;
    justify-content: space-between;
    align-items: center;
    `,
    HeadBodyCenter: styled.div`
    
    `,
    HeadBodyLeft: styled.div`
    img {
      height: 150px;
      @media(max-width: 650px){
        position: fixed;
        height: 50px;
      }
    }
    `,

    HeadLeftBlock: styled.div`
    
    `,
    HeadCenterBlock: styled.div`
    display: flex;
    justify-content: center;
    .full {
      display: flex;
      flex-direction: column;
    }
    .burger {
        display: none;
      }
    @media (max-width: 1250px){
      flex-direction: column;
    }
    @media (max-width: 650px){
      flex-direction: column;
      .full {
        display: none;
      }
      .burger {
        display: block;
      }
    }

    `,
    HeadBodyRight: styled.div`
    max-width: 30%;
    margin: 10px 0 ;
    @media (max-width: 1200px){
      h4 {
        font-size: 1rem; 
      }
    }
    @media(max-width: 650px){
      display: none;
      h4 {
        font-size: 1rem; 
        color:#fff;
      }
    }
    `,
  }
