import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { MenuContext } from '../../../context/navState';
import arrow from '../../../assets/arrow.svg';
import Button from '../Button';
import imgKabluk from '../../../assets/kabluk.png'

export const go = ()=>{
  window.open('https://vk.com/club39897450')
  return null
};

export  const imgHeader = ()=>{
  return <img src={imgKabluk}/>
};

export const informBlockHeader = ()=>{
  return (<>
    <h4>+375 29 101 66 42</h4>
    <h4>г. Гомель, ул. Юбилейная </h4>
    <h4>Киоск на ост. "Торговый центр" (старый аэродром)</h4>
    <h4>Время работы:</h4>
    <h4>ПН.-ПТ.: 9.00 до 18.00</h4>
    <h4>Cб-ВС.: выходной</h4>
  </>)
};

export const buttonBlock = ()=>{
  return (<>
        <Button name={'Связаться'} link={go}/>
        <Button name={'Услуги'} idGo={'#navBlock'}/>
        <Button name={'Местоположение'} idGo={'#mapAdres'}/>
  </>)
}

const Menu = styled.nav`
  position: fixed;
  top: 60px;
  left: 0px;
  z-index: 293;
  display: flex;
  width: 100%;
  align-items: stretch;
  background-color: #001698;
  transform: translateX(-150%);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  .buttonCenter {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    button {
      font-size: .7rem;
    }
  }
  .infRight {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    color: #fff;
    justify-content: center;
    align-items: center;
    h4 {
      font-size: .7rem;
    }
    
  }
  ${props =>
    props.open &&
    css`
      transform: translateX(0);
    `}
`;

export const SideMenu = ({ children }) => {

  const { isMenuOpen } = useContext(MenuContext);

  return <Menu open={isMenuOpen} className='1'>{children}</Menu>;
};

SideMenu.propTypes = {
  children: PropTypes.node,
};

SideMenu.defaultProps = {
  children: (
    <div>
      <div className='buttonCenter'>{buttonBlock()}</div>
      <div className='infRight'>{informBlockHeader()}</div>
    </div>
  ),
};
